.icon,
%icon {
  @include image-replace(inline-block);
  width: $spacer-m;
  height: $spacer-m;
  vertical-align: middle;
  margin-right: $spacer-xs;

  &-circle {
    border-radius: 50%;
    width: $spacer-l;
    height: $spacer-l;
    background-color: $black;
  }

  &--alert {
    @include icon('alert');
  }

  &--alert-white {
    @include icon('alert-white');
  }

